@import "../../defaultStyles";

.friends {
  min-height: 90vh;

  display: flex;
  flex-direction: column;
  justify-content: space-between;


  .subPaginator {
    width: 97%;
    display: flex;
    justify-content: space-between;
    padding: 10px 0;

    button {
      background-color: $myGray;
      border: none;
      padding: 0;
      line-height: 0;
      cursor: pointer;
    }
  }
}
